<template>
  <div>
    <b-card
      v-if="!formularioRespondido"
      class="mt-0 mb-1"
    >
      <b-row
        class="mb-1 pb-1 "
      >
        <b-col
          cols="8"
          class="d-flex justify-content-left"
        >
          <div>
            <feather-icon
              icon="ColumnsIcon"
              size="20"
              class="text-primary"
            /> <span
              class="text-primary"
              style="font-size:'18px'"
            ><strong>{{ formulario.nombre }}</strong></span>
            <h6 class="text-danger">
              USUARIO ANONIMO <small>(por favor responder todas las preguntas para probar el formulario)</small>
            </h6>
          </div>
        </b-col>
        <b-col cols="4">
          <span class="float-right bg-light-success rounded px-2"><strong class="text-success">VIGENCIA: </strong>{{ formulario.vigencia }}</span>
        </b-col>

      </b-row>
      <b-row>
        <b-col cols="12">
          <span>
            <p class="text-justify">{{ formulario.descripcion }}</p>
          </span>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12">
          <secciones
            :secciones-formulario="formulario.secciones"
            :id-usuario="idUsuario"
            @fomularioCompleto="fomularioCompleto"
          />
        </b-col>

      </b-row>
    </b-card>
    <b-card v-else>
      <b-card class="bg-light-success border-success">
        <div class="text-center">
          <h2 class="text-primary mr-1 pt-1">
            <strong>¡FORMULARIO COMPLETADO!</strong>
          </h2>
          <feather-icon
            icon="ThumbsUpIcon"
            class="text-primary"
            size="25"
          />
          <br>
          <br>
          <b-button
            size="sm"
            variant="outline-info"
            @click="()=>formularioRespondido=false"
          >
            <feather-icon
              icon="Edit2Icon"
              size="12"
            />
            Editar respuestas
          </b-button>
        </div>
      </b-card>
    </b-card>
    <!-- <btn-enviar-formulario /> -->
  </div>
</template>

<script>

import {
  BRow, BCard, BCol, BButton,
} from 'bootstrap-vue'
import store from '@/store'
import { mapState } from 'vuex'
import { onUnmounted } from '@vue/composition-api'
import formularioStoreModule from '@/views/administrador/formulario/formularioStoreModule'
import secciones from '@/views/administrador/formulario/Secciones.vue'
// import BtnEnviarFormulario from './BtnEnviarFormulario.vue'

export default {
  components: {
    BCard,
    BRow,
    BCol,
    BButton,
    // externos
    secciones,
    // BtnEnviarFormulario,
  },
  props: {
    idFormulario: {
      type: Number,
      default: 2,
    },
    idUsuario: {
      type: Number,
      default: 500,
    },
  },
  data() {
    return {
      formulario: {},
      formularioRespondido: false,
    }
  },
  computed: {
    ...mapState('formularioModule', ['respuestaCondicional']),

  },
  created() {
    if (this.$router.currentRoute.params.id_formulariox !== undefined) {
      store.state.formularioModule.formularioId = this.$router.currentRoute.params.id_formulariox
    } else {
      store.state.formularioModule.formularioId = this.idFormulario
    }
  },
  mounted() {
    store.dispatch('formularioModule/getFormulario', { usuario_id: this.idUsuario }).then(res => {
      console.log('RES:', res)
      this.formulario = res.data

      this.formulario.secciones.forEach((value, index) => {
        this.$set(this.respuestaCondicional, index, {})
        this.$set(this.respuestaCondicional[index], 'id', value.id)

        // eslint-disable-next-line eqeqeq
        if (value.condicional_respuesta == null) {
          console.log('entre null')
          this.$set(this.respuestaCondicional[index], 'value', true)
        // eslint-disable-next-line eqeqeq
        } else if (value.condicional_respuesta.respuesta == '1') {
          console.log('entre 1')
          this.$set(this.respuestaCondicional[index], 'value', false)
        } else {
          console.log('entre 0')
          this.$set(this.respuestaCondicional[index], 'value', true)
        }
      })
      console.log('---->respuestaCondicional: ', this.respuestaCondicional)
    })
  },

  methods: {
    fomularioCompleto() {
      this.formularioRespondido = true
    },
  },
  setup() {
    const FICHA_CLINCA_APP_STORE_MODULE_NAME = 'formularioModule'

    // Register module
    if (!store.hasModule(FICHA_CLINCA_APP_STORE_MODULE_NAME)) store.registerModule(FICHA_CLINCA_APP_STORE_MODULE_NAME, formularioStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(FICHA_CLINCA_APP_STORE_MODULE_NAME)) store.unregisterModule(FICHA_CLINCA_APP_STORE_MODULE_NAME)
    })
    // console.log('RUTAS:', this.$route)
    // store.state.formularioModule.formularioId = router.currentRoute.params.id // formulario seleccionado
    // formulario seleccionado
    return {
      formularioStoreModule,
    }
  },
}
</script>
